:root {
    --PrimaryColor: hsl (199, 100%, 33%);
    --SecondaryColor: hs1(187, 85%, 43%);
    --gradientColor: linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(201, 33%, 16%);
    --textColor: hsl(240, 4%, 36%);
    --whiteColorDeam: hsl(0, 0%, 93%);
    --greyText: rgb(190, 190, 190);
    --inputColor: rgb(239, 239, 239);
    --bodyColor: rgb(240, 240, 246);
    --cardBG: rgb(225, 225, 235);
    --cardHover: rgb(225, 225, 225);
}

.main {
    width: 100%;

    .secTitle {
        .title {
            position: relative;
            width: max-content;
            color: var(--textColor);
            margin: 1rem 0;
            z-index: 2;

            &::after {
                position: absolute;
                content: '';
                background: var(--SecondaryColor);
                height: 5px;
                width: 130px;
                right: 0;
                bottom: 5px;
                z-index: -1;
            }
        }
    }

    .setContent {
        justify-content: center;
        gap: 1.5rem;

        .singleDestination {
            height: 100%;
            display: grid;
            row-gap: 10px;
            /* padding: 1rem; */
            border-radius: 10px;
            align-items: center;
            background: var(--cardBG);
            box-shadow: 0 2px 4px rgba(140, 140, 141, 0.549);
            overflow: hidden;
        }

        .singleDestination :hover {
            background: var(--cardHover);
            box-shadow: 1 4px 4px rgba(85, 85, 114, 0.549);
            transition: 3s ease;
        }

        .imageDiv {
            height: 180px;
            width: 100%;
            overflow: hidden;

            .img {
                height: 100%;
                width: 100%;
                object-fit: cover;
                transform: scale(1);
                transition: 2s ease;
            }
    
            .img :hover {
                transform: scale(1.1);
            }
        }


        .cardInfo {
            padding: 1rem;

            .destTitle {
                color: var(--blackColor);
                font-size: 1.25rem;
            }

            .cantinent {
                gap: .5rem;

                .icon {
                    color: var(--textColor);
                    font-size: 18px;
                }

                .name {
                    color: var(--textColor);
                    font-weight: 400;
                    font-size: 14px;
                }
            }

            .fees {
                width: 100%;
                justify-content: space-between;
                padding: 1rem;
                margin: 1rem 0;
                border-top: 1.5px solid var(--greyText);
                border-bottom: 1.5px solid var(--greyText);

                .grade {
                    color: var(--textColor);
                    max-width: 100px;
                    line-height: 20px;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .text {
                        color: var(--textColor);
                    }

                    .small {
                        align-items: center;
                        background: var(--gradientColor);
                        border-radius: 1rem;
                        color: var(--whiteColor);
                        padding: 1px 10px;
                        font-size: 10px;
                    }
                }

                .price {
                    font-size: 2rem;
                    color: var(--blackColor);
                }
            }

            .desc {
                font-size: 13.5px;
                color: var(--textColor);
                font-weight: 500;
            }

            .btn {
                margin-top: 1.5rem;
                background: var(--gradientColor);
                color: var(--whiteColor);
                font-weight: 600;
                justify-content: space-between;
                gap: .5rem;

                .icon {
                    color: var(--whiteColor);
                    align-self: center;
                    font-size: 18px;
                }
            }
        }
    }
}

/* MEADIA QUARIES ==================================> */

@media screen and (min-width: 568px) {
    .title{
        font-size: 1.5rem;
        &::after{
            width: 115px;
        }
    }

    .setContent{
        grid-template-columns: repeat(2,1fr);

        .singleDestination{
            height: auto;
        }
    }
}

@media screen and (min-width:840px) {
    .setContent{
        grid-template-columns: repeat(3,1fr);

        .singleDestination{
            height: auto;
        }
    }
}