@media screen and (min-width:1240px) {
  .home {
    /* height: 95vh; */

    .homeContent {
      padding-left: 0;
      padding-right: 0;

      .cardDiv {
        padding: 2rem 1rem;
        grid-template-columns: repeat(3, 1fr);

        .input {
          margin-bottom: 1rem;
        }

        .searchOptions{
          bottom: -1rem;
          padding: 8px 1rem;

          .span{
            font-size: 17px;
          }
        }
      }
    }
  }
}